.Audioplayer {
    --primary: rgb(185, 0, 185);
    --secondary: orangered;
    z-index:1;
    display: flex;
    background: none;
    position: absolute;
    top: 50%;
    justify-content: center;
    justify-self: center;
    text-decoration: none;
    color: white;
    z-index: 0;
  }

.forwardbackward {
    background: none;
    border: none;
    display: flex;
    align-items: center;
    font-family: monospace;
    font-size: 18px;
    
    cursor: pointer;
}

.forwardbackward:hover {
    color: var(--primary)
}

.playpause {
   background: var(--primary);
   border: none;
   border-radius: 50%;
   width: 75px;
   height: 75px; 
   font-size: 50px;
   display: flex;
   justify-content: center;
   align-items: center;
}

.play {
    position: relative;
    left: 2px;
    z-index: 0;
}
.pause {
    position: relative;
    z-index: 0;
    
}

/* Safari styling */
.progressbar::-webkit-slider-runnable-track {
    background: var(--bar-bg);
    border-radius: 10px;
    position: relative;
    width: 100%;
    height: 11px;
    outline: none;
}

/* Firefox styling */
.progressbar::-moz-range-track {
    background: var(--bar-bg);
    border-radius: 10px;
    position: relative;
    width: 100%;
    height: 11px;
    outline: none;
}

.progressbar::-moz-focus-outer{
    border:0;
}

/* progress bar safari and chrome */
.progressbar::before {
    content: '';
    height: 11px;
    width: var(--seek-before-width);
    background-color: var(--seek-before-color);
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    cursor: pointer;
}

/* progressbar firefox */
.progressbar::-moz-range-progress {
    background-color: var(--seek-before-color);
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    height: 11px;
}

/* knobby - chrome and safari */
.progressbar::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 15px;
    width: 15px;
    border-radius: 50%;
    border:none;
    background-color: var(--knobby);
    cursor: pointer;
    position: relative;
    margin: -2px 0 0 0;
    z-index: 0;
    box-sizing: border-box;
}

.progressbar:active::-webkit-slider-thumb{
    transform: scale(1.2);
    background: var(--selectedKnobby);
}

/* knobby firefox */
.progressbar::-moz-range-thumb {
    height: 15px;
    width: 15px;
    border-radius: 50%;
    border:transparent;
    background-color: var(--knobby);
    cursor: pointer;
    position: relative;
    z-index: 0;
    box-sizing: border-box;
}

.progressbar:active::-moz-range-thumb {
    transform: scale(1.2);
    background: var(--selectedKnobby);
}

