.box {
    display: grid;
    position: absolute;
    place-items: center;
    justify-content: center;
    text-align: center;
    width: 100%;
    height: 100%;
}

.starterText3 {
    font-size: 1rem;
    background: none;
    position: absolute;
    text-decoration: none;
    font-family:fangsong;
    font-style: italic;
    font-family: Brush Script MT;
    color: red;
    z-index: 2;
    top: 175px;
}

.starterText4 {
    font-size: 1.8rem;
    background: none;
    position: absolute;
    top: 80%;
    left: 25%;
    text-decoration: none;
    
    color:  #242222;
    font-family: 'gotham.md';
    color: rgb(211, 0, 211);
    z-index:2;
   
}

